.BreadBottom{
    height: 13%;
    width: 80%;
    background: linear-gradient(#F08E4A, #e27b36);
    border-radius: 0 0 30px 30px;
    box-shadow: inset -15px 0 #C15711;
    margin: 2% auto;
}

.BreadTop{
    height: 20%;
    width: 80%;
    background: linear-gradient(#bc581e, #e27b36);
    border-radius: 50% 50% 0 0;
    box-shadow: inset -15px 0 #C15711;
    margin: 2% auto;
    position: relative;
}

.Seeds1{
    height: 15%;
    width: 10%;
    position: absolute;
    left:30%;
    top:50%;
    background-color: white;
    border-radius: 40%;
    box-shadow: inset -2px -3px #c9c9c9;
    transform: rotate(-20deg);
}

.Seeds1:after{
    content: "";
    height: 100%;
    width: 100%;
    position: absolute;
    left:-170%;
    top:-260%;
    background-color: white;
    border-radius: 40%;
    box-shadow: inset -1px 2px #c9c9c9;
    transform: rotate(60deg);
}

.Seeds1:before{
    content: "";
    height: 100%;
    width: 100%;
    position: absolute;
    left:180%;
    top:-50%;
    background-color: white;
    border-radius: 40%;
    box-shadow: inset -1px -3px #c9c9c9;
    transform: rotate(60deg);
}

.Seeds2{
    height: 15%;
    width: 10%;
    position: absolute;
    left:64%;
    top:50%;
    background-color: white;
    border-radius: 40%;
    box-shadow: inset -3px -0px #c9c9c9;
    transform: rotate(-10deg);
}

.Seeds2:before{
    content: "";
    height: 100%;
    width: 100%;
    position: absolute;
    left:150%;
    top:-20%;
    background-color: white;
    border-radius: 40%;
    box-shadow: inset 1px 3px #c9c9c9;
    transform: rotate(110deg);
}

.Meat{
    width:80%;
    height: 8%;
    left:100%;
    background: linear-gradient(#7f3608, #702e05);
    margin: 2% auto;
    border-radius: 15px;
}

.Cheese{
    width:90%;
    height: 4.5%;
    margin: 2% auto;
    background: linear-gradient(#f4d004, #d6bb22);
    border-radius: 20px;
}

.Salad {
    width:85%;
    height: 7%;
    margin: 2% auto;
    background: linear-gradient(#228c1d, #91ce50);
    border-radius: 20px;
}
.Bacon{
    width: 80%;
    height: 3%;
    background: linear-gradient(#bf3814, #c45e38);
    margin: 2% auto;
}